/* src/styles/ServicesCarousel.css */
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

.services-carousel {
  font-family: 'Lato', sans-serif;
  padding: 8rem 1rem;
  background: #f0f4ff;
  width: 100%;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
  margin: 0;
  border-radius: 0;
  box-shadow: none;
  min-height: 80vh;
  box-sizing: border-box;
}

/* Top wave decoration */
.services-carousel::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  max-width: 100%;
  height: 100px;
  background: url("data:image/svg+xml;utf8,\
<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 1440 120' preserveAspectRatio='none'>\
  <path fill='rgba(7,11,104,0.5)' d='M0,80 C360,140 1080,20 1440,80 L1440,120 L0,120 Z'/>\
</svg>") no-repeat top center;
  background-size: 100% 100%;
  z-index: 1;
  transform: rotate(180deg);
  pointer-events: none;
}

/* Bottom wave decoration */
.services-carousel::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  max-width: 100%;
  height: 100px;
  background: url("data:image/svg+xml;utf8,\
<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 1440 120' preserveAspectRatio='none'>\
  <path fill='rgba(7,11,104,1)' d='M0,80 C360,140 1080,20 1440,80 L1440,120 L0,120 Z'/>\
</svg>") no-repeat bottom center;
  background-size: 100% 100%;
  z-index: 1;
  pointer-events: none;
}

/* Background pattern */
.services-carousel-pattern {
  content: '';
  position: absolute;
  inset: 0;
  background: 
    linear-gradient(120deg, rgba(7,11,104, 0.03) 0%, transparent 50%),
    linear-gradient(-120deg, rgba(7,11,104, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 20% 20%, rgba(7,11,104, 0.05) 0%, transparent 30%),
    radial-gradient(circle at 80% 80%, rgba(7,11,104, 0.05) 0%, transparent 30%);
  pointer-events: none;
  opacity: 1;
  z-index: 0;
}

.svc-title {
  text-align: center;
  font-size: 2.8rem;
  font-weight: 700;
  color: rgb(7,11,104);
  margin: 0.5rem 0 0.25rem;
  position: relative;
  z-index: 1;
  text-shadow: 2px 2px 4px rgba(7,11,104,0.1);
}

.svc-sub {
  text-align: center;
  color: #475569;
  margin: 0.25rem 0 5rem;
  max-width: 800px;
  position: relative;
  z-index: 1;
  font-size: 1.1rem;
  line-height: 1.6;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(7,11,104,0.1);
}

/* Body wrapper */
.svc-body {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
  gap: 4rem;
  z-index: 1;
  padding: 0 2rem;
}

/* Slides container with glass effect */
.svc-carousel__slides {
  position: relative;
  width: 500px;
  height: 330px;
  overflow: visible;
  margin-right: 2rem;
  perspective: 1200px;
  transform-style: preserve-3d;
}

/* Loading state with shimmer effect */
.svc-carousel__slides.loading::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #f0f4ff 0%, #e6eeff 50%, #f0f4ff 100%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
  border-radius: 1rem;
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

/* Slide styles with 3D effect */
.svc-slide {
  position: absolute;
  left: 50%;
  width: 100%;
  height: 100%;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  transform-style: preserve-3d;
  cursor: pointer;
}

.svc-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 1rem;
  box-shadow: 0 10px 30px rgba(7,11,104,0.15);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  border: 4px solid rgb(7,11,104);
  background: white;
}

/* Previous slide */
.svc-slide.prev {
  top: 50%;
  transform: translate3d(-50%, -80%, -200px) scale(0.9);
  opacity: 0.6;
  filter: blur(1px);
  z-index: 1;
}

/* Active slide */
.svc-slide.active {
  top: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1);
  opacity: 1;
  filter: none;
  z-index: 2;
  box-shadow: 0 20px 40px rgba(7,11,104,0.2);
}

.svc-slide.active img {
  border-color: rgb(7,11,104);
  box-shadow: 0 0 0 4px rgba(7,11,104,0.2);
}

/* Next slide */
.svc-slide.next {
  top: 50%;
  transform: translate3d(-50%, -20%, -200px) scale(0.9);
  opacity: 0.6;
  filter: blur(1px);
  z-index: 1;
}

/* Add hover effects to prev and next slides */
.svc-slide.prev:hover,
.svc-slide.next:hover {
  opacity: 0.8;
  filter: blur(0px);
  transition: all 0.3s ease;
}

.svc-slide.prev:hover {
  transform: translate3d(-50%, -75%, -150px) scale(0.92);
}

.svc-slide.next:hover {
  transform: translate3d(-50%, -25%, -150px) scale(0.92);
}

/* Navigation arrows with glass effect */
.svc-arrow {
  position: absolute;
  background: rgba(255,255,255,0.8);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.2);
  color: rgb(7,11,104);
  font-size: 1.5rem;
  width: 3.5rem;
  height: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  z-index: 3;
  transition: all 0.3s ease;
  left: -4.5rem;
  box-shadow: 0 4px 15px rgba(7,11,104,0.1);
}

.svc-arrow:hover {
  background: rgba(7,11,104,0.9);
  color: white;
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(7,11,104,0.2);
}

.svc-arrow.up { top: 30%; }
.svc-arrow.down { top: 70%; }

/* Info panel with glass effect */
.svc-info {
  max-width: 450px;
  opacity: 1;
  transform: translateY(0);
  transition: all 0.4s ease;
  background: rgba(255,255,255,0.7);
  backdrop-filter: blur(10px);
  padding: 2rem;
  border-radius: 1rem;
  border: 1px solid rgba(255,255,255,0.2);
  box-shadow: 0 10px 30px rgba(7,11,104,0.1);
  text-align: center;
}

.svc-info.transitioning {
  opacity: 0;
  transform: translateY(20px);
}

.svc-info h3 {
  font-size: 2rem;
  font-weight: 700;
  color: rgb(7,11,104);
  margin-bottom: 1rem;
  text-align: center;
}

.svc-info p {
  color: #475569;
  margin-bottom: 1.5rem;
  line-height: 1.8;
  text-align: justify;
  font-size: 1.1rem;
}

.svc-info a {
  display: inline-block;
  font-weight: 700;
  color: rgb(7,11,104);
  text-decoration: none;
  padding: 0.75rem 2rem;
  border-radius: 2rem;
  background: rgba(7,11,104,0.1);
  transition: all 0.3s ease;
  margin: 0 auto;
  text-align: center;
}

.svc-info a:hover {
  background: rgb(7,11,104);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(7,11,104,0.2);
}

/* Indicators with modern style */
.svc-indicators {
  position: absolute;
  right: -3rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.svc-indicator {
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  border: 2px solid rgb(7,11,104);
  background: transparent;
  padding: 0;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.svc-indicator::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200%;
  height: 200%;
  transform: translate(-50%, -50%) scale(0);
  background: rgba(7,11,104,0.1);
  border-radius: 50%;
  transition: transform 0.3s ease;
}

.svc-indicator:hover::before {
  transform: translate(-50%, -50%) scale(1);
}

.svc-indicator.active {
  background: rgb(7,11,104);
  transform: scale(1.3);
  box-shadow: 0 0 15px rgba(7,11,104,0.3);
}

/* Responsive Design */
@media (max-width: 1280px) {
  .svc-body { 
    gap: 3rem; 
  }

  .svc-carousel__slides {
    width: 450px;
    height: 300px;
  }

  .svc-slide.prev {
    transform: translate3d(-50%, -75%, -200px) scale(0.9);
  }

  .svc-slide.next {
    transform: translate3d(-50%, -25%, -200px) scale(0.9);
  }
}

/* Enhanced Responsive Design */

/* Large desktop */
@media (max-width: 1440px) {
  .svc-title { font-size: 2.3rem; }
  .svc-body { gap: 2.5rem; }
  .svc-carousel__slides { width: 420px; height: 260px; }
}

/* Tablet */
@media (max-width: 1024px) {
  .services-carousel {
    padding: 4rem 0.5rem 2rem;
    min-height: unset;
  }
  .svc-title { font-size: 2rem; }
  .svc-sub { font-size: 1rem; margin-bottom: 2.5rem; }
  .svc-body {
    flex-direction: column;
    gap: 1.5rem;
    padding: 0 0.5rem;
    max-width: 98vw;
  }
  .svc-carousel__slides {
    width: 90vw;
    max-width: 340px;
    height: 180px;
    margin: 0 auto 1.5rem auto;
  }
  .svc-info {
    max-width: 98vw;
    padding: 1rem 0.5rem;
  }
  .svc-info h3 { font-size: 1.2rem; }
  .svc-info p { font-size: 0.98rem; }
  .svc-arrow {
    width: 2.2rem;
    height: 2.2rem;
    font-size: 1.1rem;
    left: -1.5rem;
  }
  .svc-arrow.down { right: -1.5rem; left: auto; }
  .svc-indicators {
    flex-direction: row;
    justify-content: center;
    gap: 0.7rem;
    margin: 1.2rem 0 0 0;
    position: static;
  }
}

/* Mobile */
@media (max-width: 640px) {
  .services-carousel {
    padding: 2.5rem 0.2rem 1.5rem;
    min-height: unset;
    border-radius: 0;
  }
  .svc-title {
    font-size: 1.25rem;
    margin: 0.5rem 0 0.2rem 0;
    text-align: center;
  }
  .svc-sub {
    font-size: 0.95rem;
    margin-bottom: 1.2rem;
    padding-bottom: 0.3rem;
    border-bottom: none;
    text-align: center;
  }
  .svc-body {
    flex-direction: column;
    gap: 1rem;
    padding: 0;
    max-width: 100vw;
  }
  .svc-carousel__slides {
    width: 98vw;
    max-width: 98vw;
    min-width: 0;
    height: 140px;
    margin: 0 auto 1rem auto;
  }
  .svc-slide img {
    border-radius: 0.7rem;
    border-width: 2px;
  }
  .svc-info {
    padding: 0.7rem 0.2rem;
    max-width: 100vw;
    border-radius: 0.7rem;
  }
  .svc-info h3 { font-size: 1.05rem; }
  .svc-info p { font-size: 0.92rem; }
  .svc-info a {
    padding: 0.6rem 1.2rem;
    font-size: 0.98rem;
    border-radius: 1.2rem;
  }
  .svc-arrow {
    width: 1.7rem;
    height: 1.7rem;
    font-size: 1rem;
    left: -0.7rem;
    top: 50% !important;
    transform: translateY(-50%);
  }
  .svc-arrow.down { right: -0.7rem; left: auto; }
  .svc-indicators {
    gap: 0.5rem;
    margin: 0.7rem 0 0 0;
  }
  .svc-indicator {
    width: 0.6rem;
    height: 0.6rem;
  }
}

/* Prevent overflow and horizontal scroll on all screens */
.services-carousel,
.svc-body,
.svc-carousel__slides,
.svc-info {
  box-sizing: border-box;
  overflow-x: hidden;
  max-width: 100vw;
}